.about-container {
  margin: 40px;
  /* padding-left: 50px; */
  width: 100%;
  margin-left: 80px;
}

.about-img-content {
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  padding: 15px;
  border-radius: 10px;
  padding: 20px;
}
.about-img img {
  border-radius: 15px;
  width: 100%;
  height: 380px;
}

.about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px;
}

.about-container h1 {
  color: #138781;
  font-family: cursive;
  border-bottom: 1px solid #138781;
  margin-bottom: 20px;
}

.about-container p {
  text-align: justify;
}

/* ======== DARK MODE ========= */

#dark .about-container {
  box-shadow: none;
  background-color: rgba(255, 255, 255, 0.095);
  border-radius: 10px;
}

#dark .about-content h1 {
  color: #e45353;
  border-bottom: 2px solid #b0a4a4;
}

#dark .about-content p {
  color: white;
}
/* ======== Mobile ========= */
@media (max-width: 600px) {
  .about-container {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
  }
  .about-content h1 {
    margin-top: 10px;
  }
}
