.projects-container {
  padding-left: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
  margin-top: 30px;
}

.projects-container h2 {
  font-weight: bold;
  color: #138781;
}

.projects-container hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.projects-container p {
  color: #717676;
  text-align: center;
}

.projects-container .card-container {
  margin-top: 50px;
}

.flip-card {
  background-color: transparent;
  max-width: 350px;
  height: 350px;
  perspective: 1000px;
  font-family: sans-serif;
}

.title {
  font-size: 1.2em;
  font-weight: 900;
  text-align: center;
  margin: 0;
}

.flip-card-inner {
  position: relative;
  width: 250px;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-back img {
  height: 220px;
  width: 100%;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

.flip-card-front,
.flip-card-back {
  box-shadow: 0 8px 14px 0 rgba(0, 0, 0, 0.2);
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 1rem;
}

.flip-card-front {
  background: linear-gradient(120deg, #ffdde8 20%, #b4fffb 88%, #8bf5f0 75%);
  color: black;
  border: 1px solid #302b63;
  padding: 10px;
}

.flip-card-front h3 {
  margin-top: 5px;
  color: #212125;
}

.flip-card-front p {
  margin-top: 5px;
  color: #46464b;
  text-align: inherit;
}

.flip-card-back {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  transform: rotateY(180deg);
  border: 1px solid #138781;
}

.card-image-overly span {
  margin-left: 8px;
  font-size: 14px;
  background-color: #e0b50f;
  padding: 5px;
  color: black;
  border-radius: 10px;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  padding-bottom: 25px;
}

.buttons-container .btn-project {
  margin-left: 10px;
  border: 1px solid #138781;
}

/* =============================== */
@media (max-width: 600px) {
  .projects-container {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  .projects-container hr {
    width: 280px;
  }
  .project-container .paragraph {
    justify-content: center;
  }

  .cards-container {
    padding-left: 0px;
  }

  .projects-container .project-card {
    margin-top: 30px;
  }

  .cards-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
