.home-container {
  height: 300px;
  color: white;
  padding-left: 170px;
}

.home-content h2 {
  /* padding: 10px; */
  padding-top: 70px;
}

.home-content h1 {
  color: rgb(203, 203, 30);
}

.home-buttons {
  margin-top: 20px;
}

.home-buttons .btn-hire {
  background-color: #3b8ff3;
  color: white;
  border: none;
  font-size: 16px/1.5;
  cursor: pointer;
  margin-right: 10px;
  width: 120px;
}

.home-buttons .btn-hire:hover {
  background-color: white;
  color: #3b8ff3;
  width: 130px;
  transition: 1s;
}

.home-buttons .btn-cv {
  color: white;
  background-color: #824d11; /* Green */
  border: none;
  cursor: pointer;
  /* opacity: 60%; */
}

.home-buttons .btn-cv:hover {
  opacity: 100%;
  transition: 1s;
  background-color: #f5a207;
}

.theme-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  background-color: #1e1e1e;
  margin-top: 20px;
  text-align: center;
  position: fixed;
  right: 0;
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  z-index: 1;
}

/* =============== */
@media (max-width: 600px) {
  .home-container {
    padding: 0;
    margin: 0;
    margin-top: 50px;
  }
  .home-container h2 {
    font-size: 24px;
  }
  .home-container h1 {
    font-size: 24px;
  }
}
