.navbar-profile-pic img {
  height: 100px;
  width: 100px;
  border-radius: 75%;
  background-image: cover;
  display: flex;
  vertical-align: center;
  margin: 0 auto;
}

.nav-item {
  color: white;
  margin-top: 30px;
  /* text-align: center; */
  margin-left: 25px;
}

.nav-link {
  margin-top: 30px;
  cursor: pointer;
}

.nav-link svg {
  margin-right: 10px;
}

.nav-link:hover {
  color: chocolate;
  /* font-size: 17px; */
}

.nav-link .active {
  color: #f29f67;
  border-left: 5px solid #f29f67;
  padding-left: 10px;
  text-decoration: none;
}
