.education {
  padding-left: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  margin-left: 50px;
  width: 100%;
  padding: 30px;
  border-radius: 10px;
}

.education .vertical-timeline-element-title {
  color: #138781;
  font-size: 25px;
  margin-bottom: 10px;
}

.education .vertical-timeline-element-subtitle {
  color: #1e1e2c;
  font-size: 20px;
}

.education h2 {
  font-weight: bold;
  color: #138781;
  margin-bottom: 0px;
}

.education .time-line-content p {
  font-size: 15px;
  color: #afb3a4;
  text-align: justify;
}

.education hr {
  color: #138781;
  width: 300px;
  margin-bottom: 70px;
}

.time-line-content {
  border-top: 3px solid #e17228;
  background-color: white;
  width: 100%;
  padding-top: 20px;
}

.trigger {
  animation-duration: 2s;
}

/* ========= DARK MODE ============ */

#dark .education {
  background-color: rgba(255, 255, 255, 0.095);
}

#dark .education h2 {
  font-weight: bold;
  color: #e45353;
}

#dark .education hr {
  width: 300px;
  margin: 10px auto;
  margin-bottom: 40px;
  border-bottom: 2px solid #b0a4a4;
}

#dark .time-line-content {
  /* background-color: #e17228; */
}

/* =========== Mobile Responsive=============== */
@media (max-width: 600px) {
  .education {
    margin: 0;
    padding: 0;
    margin-top: 30px;
  }
  .education h2 {
    padding-top: 20px;
  }
  .education hr {
    width: 280px;
    margin-bottom: 10px;
  }
}
