.tech-stack {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 100px;
  margin-top: 30px;
  margin-bottom: 20px;
}

.tech-stack h2 {
  font-weight: bold;
  color: #138781;
}

.tech-stack hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.tech-stack p {
  color: #787b7b;
  text-align: center;
}

.tech-stack .card {
  cursor: pointer;
}

.tech-stack .tech-icon {
  font-size: 20px;
  color: deeppink;
  /* color: white; */
}

.tech-stack .card:hover {
  background-color: #138781;
  color: white;
  transition: all 0.6s ease-in-out;
}

/* =============================== */
@media (max-width: 600px) {
  .tech-stack {
    padding: 0;
    margin: 0;
    margin-top: 50px;
  }

  .tech-stack hr {
    width: 280px;
  }
}
